@mixin onTablet {
  @media (min-width: 744px) {
    @content;

  }
}

@mixin onDesktop {
  @media (min-width: 1260px) {
    @content;

  }
}

@mixin top-actions {
  --icon-width: 18px;
  display: grid;
  grid-template-columns: 1fr var(--icon-width) var(--icon-width);
  gap: 30px;
  align-items: center;

  @include onTablet() {
    --icon-width: 24px;
    gap: 40px;
  }
}
